import React, {Component} from 'react';
import { connect } from 'react-redux';

import {
  DEFAULT,
 } from '../App/constants';

import {
  defaultAction,
} from '../App/actions'


class HomePage extends React.Component { // eslint-disable-line react/prefer-stateless-function

  onClickOnButton = () => {
    this.props.onDefaultAction();
  }

  render() {
    const { loading, message, messageDelay } = this.props;

    let messageContent = ( <div> <p>On Click this message will change</p> </div> );
    if( message ) { messageContent = ( <div> <p>{message}</p> </div> ) }

    let messageDelayContent = (<div> <p>On Click this message will change with a 1000ms delay </p> </div>);
    if( messageDelay ) { messageDelayContent = ( <div> <p>{messageDelay}</p></div> ) }

    return (
      <div>
        <p><button onClick={this.onClickOnButton}> Click me </button></p>
        {messageContent}
        {messageDelayContent}
        <p>
          To aget started, edit <code>src/HomePage/index.js</code> and save to reload.
        </p>
      </div>
    );
  }
}


// export default App;
const mapStateToProps = (state) => {
  return {
    loading: state.getIn(['app','loading']),
    message: state.getIn(['app','message']),
    messageDelay: state.getIn(['app','nested','message'])
  };
};

function mapDispatchToProps(dispatch) {
  return {
    onDefaultAction: () => dispatch(defaultAction()),
    dispatch,
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(HomePage);



